<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 车间编码 -->
            <a-form-model-item
              label="车间编码"
              prop="workCenterCode"
              ref="workCenterCode"
            >
              <a-input
                v-model="entity.workCenterCode"
                placeholder="请输入车间编码"
                name="workCenterCode"
                :disabled="!!ndoEditParams.row"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 车间名称 -->
            <a-form-model-item
              label="车间名称"
              prop="name"
              ref="name"
            >
              <a-input
                v-model="entity.name"
                placeholder="请输入车间名称"
                name="name"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 车间类型 -->
            <a-form-model-item
              label="车间类型"
              prop="workCenterType"
              ref="workCenterType"
            >
              <a-select
                v-model="entity.workCenterType"
                placeholder="请选择车间类型"
                name="workCenterType"
                @change="inChange"
              >
                <a-select-option value="机加工车间">机加工车间</a-select-option>
                <a-select-option value="装配车间">装配车间</a-select-option>
                <a-select-option value="热处理车间">热处理车间</a-select-option>
                <a-select-option value="表面处理车间">表面处理车间</a-select-option>
                <a-select-option value="包装车间">包装车间</a-select-option>
                <a-select-option value="仓储车间">仓储车间</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 所属工厂 -->
            <a-form-model-item
              label="所属工厂"
              prop="factoryId"
              ref="factoryId"
            >
              <a-select
                v-model="entity.factoryId"
                placeholder="请选择所属工厂"
                name="factoryId"
                @change="handleFactoryChange"
              >
                <a-select-option value="1">生产工厂1</a-select-option>
                <a-select-option value="2">组装工厂2</a-select-option>
                <a-select-option value="3">加工工厂3</a-select-option>
                <a-select-option value="4">储存中心4</a-select-option>
                <a-select-option value="5">物流中心5</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 日产能 -->
            <a-form-model-item
              label="日产能"
              prop="capacity"
              ref="capacity"
            >
              <a-input
                v-model="entity.capacity"
                placeholder="请输入日产能"
                name="capacity"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 建立时间 -->
            <a-form-model-item
              label="建立时间"
              prop="foundDate"
              ref="foundDate"
            >
              <a-date-picker
                v-model="entity.foundDate"
                style="width: 100%"
                name="foundDate"
                @change="inChange"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 车间面积 -->
            <a-form-model-item
              label="车间面积"
              prop="area"
              ref="area"
            >
              <a-input
                v-model="entity.area"
                placeholder="请输入车间面积"
                name="area"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 人员数量 -->
            <a-form-model-item
              label="人员数量"
              prop="staffCount"
              ref="staffCount"
            >
              <a-input-number
                v-model="entity.staffCount"
                :min="1"
                :max="1000"
                style="width: 100%"
                name="staffCount"
                @change="inChange"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 运行状态 -->
            <a-form-model-item
              label="运行状态"
              prop="status"
              ref="status"
            >
              <a-select
                v-model="entity.status"
                placeholder="请选择运行状态"
                name="status"
                @change="inChange"
              >
                <a-select-option value="运行中">运行中</a-select-option>
                <a-select-option value="维护中">维护中</a-select-option>
                <a-select-option value="停产">停产</a-select-option>
                <a-select-option value="调试中">调试中</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="18">
            <!-- 描述 -->
            <a-form-model-item label="描述" prop="description">
              <a-textarea
                v-model="entity.description"
                placeholder="请输入描述信息"
                :rows="4"
                :max-length="1000"
                name="description"
                @change="inChange"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>

      <!-- 设备信息 -->
      <g-collapse headerName="设备信息">
        <template #content>
          <div class="mb-4">
            <a-button type="primary" @click="addEquipment">
              <a-icon type="plus" />添加设备
            </a-button>
          </div>
          <a-table
            :columns="equipmentColumns"
            :data-source="entity.equipmentList"
            :pagination="false"
            rowKey="id"
          >
            <template #action="{ record }">
              <a-space>
                <a @click="editEquipment(record)">编辑</a>
                <a-divider type="vertical" />
                <a-popconfirm
                  title="确定要删除该设备吗？"
                  @confirm="deleteEquipment(record)"
                >
                  <a>删除</a>
                </a-popconfirm>
              </a-space>
            </template>
          </a-table>
        </template>
      </g-collapse>

      <!-- 人员信息 -->
      <g-collapse headerName="人员信息">
        <template #content>
          <div class="mb-4">
            <a-button type="primary" @click="addStaff">
              <a-icon type="plus" />添加人员
            </a-button>
          </div>
          <a-table
            :columns="staffColumns"
            :data-source="entity.staffList"
            :pagination="false"
            rowKey="id"
          >
            <template #action="{ record }">
              <a-space>
                <a @click="editStaff(record)">编辑</a>
                <a-divider type="vertical" />
                <a-popconfirm
                  title="确定要删除该人员吗？"
                  @confirm="deleteStaff(record)"
                >
                  <a>删除</a>
                </a-popconfirm>
              </a-space>
            </template>
          </a-table>
        </template>
      </g-collapse>

      <!-- 元数据信息 -->
      <g-collapse headerName="元数据信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>{{ entity.createdBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 创建时间 -->
              <a-form-model-item label="创建时间">
                <span>{{ entity.createdTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改人 -->
              <a-form-model-item label="修改人">
                <span>{{ entity.modifiedBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改时间 -->
              <a-form-model-item label="修改时间">
                <span>{{ entity.modifiedTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditWorkCenter",
  mixins: [GNdoPageEditBase],
  data() {
    return {
      entity: {
        workCenterCode: "",
        name: "",
        workCenterType: "",
        factoryId: "",
        factoryName: "",
        capacity: "",
        foundDate: "",
        area: "",
        staffCount: 1,
        status: "",
        description: "",
        equipmentList: [],
        staffList: [],
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: ""
      },
      loading: false,
      rules: {
        workCenterCode: [
          { required: true, message: "请输入车间编码", trigger: "blur" },
          { pattern: /^WC\d{4}$/, message: "车间编码必须为WC+4位数字", trigger: "blur" }
        ],
        name: [
          { required: true, message: "请输入车间名称", trigger: "blur" },
          { min: 2, max: 50, message: "长度在 2 到 50 个字符", trigger: "blur" }
        ],
        workCenterType: [
          { required: true, message: "请选择车间类型", trigger: "change" }
        ],
        factoryId: [
          { required: true, message: "请选择所属工厂", trigger: "change" }
        ],
        capacity: [
          { required: true, message: "请输入日产能", trigger: "blur" },
          { pattern: /^\d+件\/天$/, message: "格式必须为数字+件/天", trigger: "blur" }
        ],
        foundDate: [
          { required: true, message: "请选择建立时间", trigger: "change" }
        ],
        area: [
          { required: true, message: "请输入车间面积", trigger: "blur" },
          { pattern: /^\d+平方米$/, message: "格式必须为数字+平方米", trigger: "blur" }
        ],
        staffCount: [
          { required: true, message: "请输入人员数量", trigger: "change" },
          { type: "number", min: 1, message: "人员数量必须大于0", trigger: "change" }
        ],
        status: [
          { required: true, message: "请选择运行状态", trigger: "change" }
        ]
      },
      equipmentColumns: [
        {
          title: "序号",
          dataIndex: "index",
          width: 60,
          customRender: (text, record, index) => index + 1
        },
        {
          title: "设备编码",
          dataIndex: "equipmentCode",
          width: 120
        },
        {
          title: "设备名称",
          dataIndex: "equipmentName",
          width: 150
        },
        {
          title: "设备类型",
          dataIndex: "equipmentType",
          width: 120
        },
        {
          title: "设备状态",
          dataIndex: "equipmentStatus",
          width: 100
        },
        {
          title: "操作",
          dataIndex: "action",
          width: 150,
          scopedSlots: { customRender: "action" }
        }
      ],
      staffColumns: [
        {
          title: "序号",
          dataIndex: "index",
          width: 60,
          customRender: (text, record, index) => index + 1
        },
        {
          title: "工号",
          dataIndex: "employeeId",
          width: 100
        },
        {
          title: "姓名",
          dataIndex: "name",
          width: 100
        },
        {
          title: "岗位",
          dataIndex: "position",
          width: 120
        },
        {
          title: "技能等级",
          dataIndex: "skillLevel",
          width: 100
        },
        {
          title: "操作",
          dataIndex: "action",
          width: 150,
          scopedSlots: { customRender: "action" }
        }
      ]
    };
  },
  methods: {
    // 初始化
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求
        setTimeout(() => {
          this.loading = false;
          this.entity = {
            workCenterCode: "WC0001",
            name: "机加工车间1",
            workCenterType: "机加工车间",
            factoryId: "1",
            factoryName: "生产工厂1",
            capacity: "1000件/天",
            foundDate: "2024-01-01",
            area: "500平方米",
            staffCount: 30,
            status: "运行中",
            description: "用于机械加工的车间，配备数控机床等设备",
            equipmentList: [
              {
                id: 1,
                equipmentCode: "EQ001",
                equipmentName: "数控车床",
                equipmentType: "加工设备",
                equipmentStatus: "运行中"
              }
            ],
            staffList: [
              {
                id: 1,
                employeeId: "EMP001",
                name: "张三",
                position: "操作工",
                skillLevel: "高级"
              }
            ],
            createdBy: "admin",
            createdTime: "2024-03-19 10:00",
            modifiedBy: "admin",
            modifiedTime: "2024-03-19 15:00"
          };
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },
    resetFormData() {
      // 重置表单绑定的数据源
      this.entity = {
        workCenterCode: "",
        name: "",
        workCenterType: "",
        factoryId: "",
        factoryName: "",
        capacity: "",
        foundDate: "",
        area: "",
        staffCount: 1,
        status: "",
        description: "",
        equipmentList: [],
        staffList: [],
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: ""
      };
    },
    async save() {
      // 保存 校验
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.name);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },
    inChange(e) {
      console.log(e.target.value);
    },
    handleFactoryChange(value) {
      const factoryMap = {
        "1": "生产工厂1",
        "2": "组装工厂2",
        "3": "加工工厂3",
        "4": "储存中心4",
        "5": "物流中心5"
      };
      this.entity.factoryName = factoryMap[value];
    },
    addEquipment() {
      this.entity.equipmentList.push({
        id: this.entity.equipmentList.length + 1,
        equipmentCode: `EQ${String(this.entity.equipmentList.length + 1).padStart(3, "0")}`,
        equipmentName: "",
        equipmentType: "",
        equipmentStatus: "运行中"
      });
    },
    editEquipment(record) {
      // 实现编辑设备的逻辑
      console.log("编辑设备", record);
    },
    deleteEquipment(record) {
      const index = this.entity.equipmentList.findIndex(item => item.id === record.id);
      if (index > -1) {
        this.entity.equipmentList.splice(index, 1);
      }
    },
    addStaff() {
      this.entity.staffList.push({
        id: this.entity.staffList.length + 1,
        employeeId: `EMP${String(this.entity.staffList.length + 1).padStart(3, "0")}`,
        name: "",
        position: "",
        skillLevel: "初级"
      });
    },
    editStaff(record) {
      // 实现编辑人员的逻辑
      console.log("编辑人员", record);
    },
    deleteStaff(record) {
      const index = this.entity.staffList.findIndex(item => item.id === record.id);
      if (index > -1) {
        this.entity.staffList.splice(index, 1);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.mb-4 {
  margin-bottom: 16px;
}
</style> 