<template>
  <g-body-layout
    :pageIndex="pageNumber"
    :pageSize="pageSize"
    :total="total"
    :filterText.sync="filterText"
    @pageIndexChange="pageIndexChange"
    @pageSizeChange="pageSizeChange"
    @suffixSearchCallback="onFilterReady"
  >
    <!-- 按钮组插槽 -->
    <template #btnGroup>
      <g-btn-group :buttonLists="btnList" @btnCallBack="btnGroupClick">
      </g-btn-group>
    </template>
    <!-- g-ndo-table插槽 -->
    <template #table>
      <g-ndo-table
        :id="id"
        :handName="'车间建模'"
        :dataList="dataList"
        :isTableLoading="loading"
        :total="total"
        @rowClick="rowClick"
      ></g-ndo-table>
    </template>
    <!-- 详细内容插槽 -->
    <template #content>
      <create-or-edit-work-center
        :ndoEditParams="ndoEditParams"
        :refreshTag="refreshTag"
        :saveTag="saveTag"
        :resetTag="resetTag"
        @childRefresh="childRefresh($event)"
        @rowClick="rowClick($event)"
        v-if="isShowEdit"
      ></create-or-edit-work-center>
      <g-empty v-else> </g-empty>
    </template>
  </g-body-layout>
</template>

<script>
import CreateOrEditWorkCenter from "./create-or-edit-work-center/create-or-edit-work-center.vue";
import GCopyNdo from "@/components/g-copy-ndo/g-copy-ndo.vue";
import { GNdoPageBase } from "@/utils";

export default {
  name: "WorkCenter",
  mixins: [GNdoPageBase],
  components: {
    CreateOrEditWorkCenter,
  },

  data() {
    return {
      isTableLoading: false,
      filterText: "",
    };
  },

  mounted() {},
  created() {
    this.getBtnList();
  },
  methods: {
    getBtnList() {
      // 获取按钮列表
      this.btnList = [
        {
          text: this.AppConst.button.create,
          icon: this.AppConst.icon.create,
          eventName: this.AppConst.button.create,
          display: true,
        },
        {
          text: this.AppConst.button.copy,
          icon: this.AppConst.icon.copy,
          eventName: this.AppConst.button.copy,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.delete,
          icon: this.AppConst.icon.delete,
          eventName: this.AppConst.button.delete,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.refresh,
          icon: this.AppConst.icon.refresh,
          eventName: this.AppConst.button.refresh,
          right: true,
          display: this.isShowEdit,
        },
        {
          text: this.AppConst.button.save,
          icon: this.AppConst.icon.save,
          eventName: this.AppConst.button.save,
          right: true,
          type: "primary",
          display: this.isShowEdit,
        },
      ];
    },
    copy() {
      this.createModal(
        { title: "复制", width: "600px" },
        {
          component: GCopyNdo,
          props: {
            entity: {
              copyValue: this.dataList[0].name,
              value: this.dataList[0].name,
            },
          },
          on: {
            success: (res) => {
              console.log(res);
              // 手动延时
              this.loading = true;
              setTimeout(() => {
                this.loading = false;
                this.copySuccess(res.value, res.copyValue);
                this.refreshGoFirstPage();
              }, 500);
            },
          },
        }
      );
    },
    delete() {
      // 删除确认
      this.$confirm({
        title: "确认删除",
        content: "确认删除该车间吗？删除后无法恢复。",
        onOk: () => {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            // 消息提示
            this.deleteSuccess();
            // 刷新
            this.refresh();
          }, 500);
        },
      });
    },
    fetchData(request) {
      this.loading = true;
      setTimeout(() => {
        request.finishedCallback();
        // 填充dataList 示例数据
        let dataList = [];
        const workCenterTypes = [
          "机加工车间",
          "装配车间",
          "热处理车间",
          "表面处理车间",
          "包装车间",
          "仓储车间"
        ];
        
        const factoryNames = [
          "生产工厂1",
          "组装工厂2",
          "加工工厂3",
          "储存中心4",
          "物流中心5"
        ];
        
        const statuses = ["运行中", "维护中", "停产", "调试中"];
        const capacities = ["1000件/天", "2000件/天", "3000件/天", "4000件/天", "5000件/天"];
        
        for (let i = 0; i < 25; i++) {
          const typeIndex = i % 6;
          const factoryIndex = i % 5;
          const statusIndex = i % 4;
          const capacityIndex = i % 5;
          
          // 生成随机的车间编码
          const workCenterCode = `WC${String(i + 1).padStart(4, "0")}`;
          
          // 生成随机的建立时间
          const foundDate = new Date(2000 + i % 20, i % 12, 1 + i % 27);
          const foundDateStr = `${foundDate.getFullYear()}-${(foundDate.getMonth() + 1).toString().padStart(2, '0')}-${foundDate.getDate().toString().padStart(2, '0')}`;

          dataList[i] = {
            id: i + 1,
            workCenterCode: workCenterCode,
            name: `${workCenterTypes[typeIndex]}${i + 1}`,
            workCenterType: workCenterTypes[typeIndex],
            factoryName: factoryNames[factoryIndex],
            factoryId: factoryIndex + 1,
            capacity: capacities[capacityIndex],
            foundDate: foundDateStr,
            area: (500 + i * 100) + "平方米",
            staffCount: 30 + i * 10,
            description: `${factoryNames[factoryIndex]}的${workCenterTypes[typeIndex]}，日产能${capacities[capacityIndex]}，配备${30 + i * 10}名工作人员。`,
            status: statuses[statusIndex],
            createdBy: "admin",
            createdTime: "2024-03-19 " + (10 + i % 10) + ":" + (i % 60),
            modifiedBy: "admin",
            modifiedTime: "2024-03-" + (20 + i % 8) + " " + (13 + i % 10) + ":" + (i % 60),
          };
        }

        // filter 过滤
        if (this.filterText) {
          dataList = dataList.filter((item) => {
            return item.workCenterCode.indexOf(this.filterText) > -1 || 
                   item.name.indexOf(this.filterText) > -1 ||
                   item.workCenterType.indexOf(this.filterText) > -1 ||
                   item.factoryName.indexOf(this.filterText) > -1 ||
                   item.capacity.indexOf(this.filterText) > -1 ||
                   item.description.indexOf(this.filterText) > -1;
          });
        }

        request.successCallback({
          items: dataList,
          totalCount: dataList.length * 2,
        });
      }, 1000);
    },
  },
};
</script>

<style lang="scss" scoped></style>